import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {createStore} from 'redux';
import{Provider} from 'react-redux';
const VADIVELU_COMEDY ='VADIVELU_COMEDY';
const SOORI_COMEDY = 'SOORI_COMEDY'; 

 export const vadiveluAction =()=>({type:VADIVELU_COMEDY}) 
 export const sooriAction =()=>({type:SOORI_COMEDY}) 

const comedyReducer = (state,actions) => {
  switch(actions.type){
    case VADIVELU_COMEDY:
      return state ="louu"
      case SOORI_COMEDY:
        return state ="haaa"
      
      default:
        return state ="no comedy"
  }
}
let store = createStore(comedyReducer);



ReactDOM.render(<Provider store ={store}>
  <App/></Provider>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
